<template>
	<el-row>
		<el-col :xs="4" :sm="4" :md="4" :lg="4">
			<div class="grid-content bg-purple">
			</div>
		</el-col>
		<el-col :xs="3" :sm="3" :md="3" :lg="3" :offset="16">
			<div class="grid-content bg-purple">
				<el-menu class="el-menu-demo" router :default-active="$route.path" mode="horizontal" background-color="#B3C0D1" text-color="#333" active-text-color="#409EFF" @select="headerSelect">
					<el-submenu index="1">
						<template slot="title">
							<i class="el-icon-location"></i>
							<span slot="title">当前用户</span>
						</template>
						<el-menu-item index="/user/BaseInfo">基本资料</el-menu-item>
						<el-menu-item index="/user/MyMessage">我的消息</el-menu-item>
						<el-menu-item index="/user/SecuritySetting">安全设置</el-menu-item>
						<el-menu-item index="/user/exit">退出</el-menu-item>
					</el-submenu>
				</el-menu>
			</div>
		</el-col>
	</el-row>
</template>

<script>
	export default {
		methods: {
			headerSelect(key, keyPath){
				if(key === '/user/exit'){
					this.$emit('listenerLogin', false);
				}
			}
		}
	}
</script>

<style scoped="scoped">
	.el-row,
	.el-col {
		height: 100%;
	}
	
	.bg-purple {
		text-align: center;
	}
	
	.grid-content {
		height: 100%;
	}
	a{
		text-decoration: none;
	}
</style>


