<template>
	<div class="login">
		<el-card class="box-card">
			<el-row>
				<el-col :span="24">
					<div class="grid-content">
						<div style="width: 400px;height: 200px; ;margin: 0 auto;">LOGO</div>
					</div>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="24">
					<div class="grid-content">
						<el-form ref="form" status-icon :rules="rules" :model="form" label-width="80px">
							<el-form-item label="用户名" prop="name">
								<el-input v-model="form.name" placeholder="请输入用户名"></el-input>
							</el-form-item>
							<el-form-item label="密码" prop="pwd">
								<el-input type="password" v-model="form.pwd" placeholder="请输入密码"></el-input>
							</el-form-item>
							<el-form-item style="text-align: right;">
								<el-button type="primary" @click="onSubmit('form')">登录</el-button>
								<el-button @click="onReset('form')">清空</el-button>
							</el-form-item>
						</el-form>
					</div>
				</el-col>
			</el-row>
		</el-card>
	</div>
</template>

<script>
	export default {
		name: 'Login',
		data() {
			return {
				form: {
					name: '',
					pwd: ''
				},
				rules: {
					name: [{
							required: true,
							message: '请输入用户名',
							trigger: 'change'
						},
						{
							min: 2,
							max: 10,
							message: '长度在 2 到 10 个字符',
							trigger: 'change'
						}
					],
					pwd: [{
							required: true,
							message: '请输入密码',
							trigger: 'change'
						},
						{
							min: 6,
							max: 12,
							message: '长度在6 到 12 个字符',
							trigger: 'change'
						}
					]
				}
			}
		},
		created() {
			this.$emit('listenerLogin', this.listenerLogin());
		},
		methods: {
			listenerLogin() {
				return true;
			},
			onSubmit(formName) {
				const _this = this;
				this.$refs[formName].validate((valid) => {
					if(valid) {
						//console.log(_this.form);
						_this.$emit('listenerLogin', false);
					} else {
						console.log('error submit!!');
						return false;
					}
				});
			},
			onReset(formName) {
				this.$refs[formName].resetFields();
			}
		}
	}
</script>

<style scoped="scoped">
	.el-row {
		margin: 30px;
	}
	
	.el-form {
		width: 400px;
		margin: 0 auto;
	}
	
	.login {
		width: 800px;
		height: 500px;
		margin: 0 auto;
		padding-top: 40px;
	}
	
	.box-card {
		width: 100%;
		height: 100%;
		text-align: center;
		padding: 15px;
	}
</style>


