<template>
	<div id="app">
		<Login @listenerLogin="listenerLogin" v-if="!isLogin" />
		<el-container v-if="isLogin">
			<el-header>
				<MyHeader @listenerLogin="listenerLogin" />
			</el-header>
			<el-container>
				<el-aside width="200px">
					<MyAside />
				</el-aside>
				<el-container>
					<el-main>
						<MyMain />
					</el-main>
					<el-footer>
						<MyFooter />
					</el-footer>
				</el-container>
			</el-container>
		</el-container>
	</div>
</template>

<script>
	import MyHeader from '@/components/MyHeader';
	import MyFooter from '@/components/MyFooter';
	import MyMain from '@/components/MyMain';
	import MyAside from '@/components/MyAside';
	import Login from '@/components/Login';

	export default {
		name: 'app',
		components: {
			MyHeader,
			MyFooter,
			MyMain,
			MyAside,
			Login
		},
		data() {
			return {
				isLogin: false,
				isExit: false
			}
		},
		methods: {
			listenerLogin(word) {
				console.log(word);
				this.isLogin = word;
			}
		}
	}
</script>

<style>
	html,
	body,
	#app,
	#app>.el-container {
		padding: 0;
		margin: 0;
		width: 100%;
		height: 100%;
		font-size: 62.5%;
	}
	
	.el-header,
	.el-footer {
		background-color: #B3C0D1;
		color: #333;
		height: 10%;
		margin: 0;
	}
	
	.el-aside {
		background-color: #D3DCE6;
		color: #333;
	}
	
	.el-main {
		background-color: #E9EEF3;
		color: #333;
		height: 80%;
		overflow: auto;
	}
</style>


